.benefits {
  @media (min-width: 768px) {
    border-bottom: 1px solid var(--special-grey2, #e6e6e6);
    padding-bottom: 36px;
  }

  @media (min-width: 1280px) {
    border: none;
    padding: 0;
  }
}

.benefits__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(2, 37%);
    // grid-template-rows: 1fr 1fr;
    grid-template-rows: repeat(2, max-content);
    // justify-content: center;
    column-gap: 122px;
    row-gap: 27px;
  }

  @media (min-width: 1280px) {
    grid-template-columns: repeat(4, 20%);
    grid-template-rows: 1fr;
    justify-content: space-between;
    gap: initial;
  }
}

.benefits__item {
  --block-font-special: var(--oswald, "Tahoma", sans-serif);
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    margin: 0;
    padding-top: 40px;
    padding-bottom: 57px;
    position: relative;

    &--has_digit::before {
      content: "1";
      position: absolute;
      font-family: var(--block-font-special);
      font-size: 280px;
      line-height: 1;
      color: var(--basic-grey);
      top: 0;
      right: 34px;
      z-index: -4;

      @media (min-width: 1280px) {
        right: 14px;
      }
    }

    &--number_2::before {
      content: "2";
      right: 17px;

      @media (min-width: 1280px) {
        right: 0;
      }
    }

    &--number_3::before {
      content: "3";
      right: 13px;

      @media (min-width: 1280px) {
        right: -2px;
      }
    }

    &--number_4::before {
      content: "4";
      right: 14px;

      @media (min-width: 1280px) {
        right: -3px;
      }
    }
  }
}

.benefits__title {
  margin: 0;
  margin-bottom: 36px;

  @media (min-width: 768px) {
    margin-bottom: 29px;
  }
}

.benefits__text {
  display: inline-flex;
  align-items: center;
  margin: 0;

  @media (min-width: 768px) {
    flex-direction: column;
    align-items: initial;
  }

  // &--has_icon {
  //   --icon-a: url("../../img/stack.svg#benef-ico-1");
  //   --icon-b: url("../../img/stack.svg#benef-ico-2");
  //   --icon-c: url("../../img/stack.svg#benef-ico-3");
  //   --icon-d: url("../../img/stack.svg#benef-ico-4");
  // }

  &--has_icon::before {
    content: "";
    min-width: 60px;
    min-height: 60px;
    margin-right: 20px;
    background-image: url("../../img/stack.svg#benef-ico-1");
    background-repeat: no-repeat;

    @media (min-width: 768px) {
      min-width: 80px;
      min-height: 80px;
      margin-bottom: 25px;
    }
  }

  &--icon_b::before {
    background-image: url("../../img/stack.svg#benef-ico-2");
  }

  &--icon_c::before {
    background-image: url("../../img/stack.svg#benef-ico-3");
  }

  &--icon_d::before {
    background-image: url("../../img/stack.svg#benef-ico-4");
  }
}
