.product-categories {
  --block-font-special: var(--oswald, "Tahoma", sans-serif);
}

.product-categories__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;

  @media (min-width: 1280px) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.product-categories__item {
  background-color: var(--basic-grey, #f2f2f2);
  padding: 21px 20px 13px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    padding: 66px 22px 40px 52px;
    margin-bottom: 30px;
    display: grid;
    grid-template-columns: 1fr min-content;
    grid-template-rows: min-content 1fr min-content;
  }

  @media (min-width: 1280px) {
    padding-top: 77px;
    padding-bottom: 41px;
    margin-bottom: 0;
    // display: grid;
    grid-template-columns: min-content 1fr;
    grid-template-rows: min-content 1fr min-content;
  }

  &--has_icon {
    // --icon-a: url("../../img/stack.svg#cat-slim"); /* Убрал чтобы сработало на iOS */
    // --icon-b: url("../../img/stack.svg#cat-pro");

    &::before {
      content: "";
      float: left;
      margin-left: -7px;
      margin-right: 9px;
      margin-top: -7px;
      width: 70px;
      height: 70px;
      background-image: url("../../img/stack.svg#cat-slim");

      @media (min-width: 768px) {
        width: 280px;
        height: 280px;
        float: none;
        margin-left: -18px;
        margin-right: 0;
        margin-top: -15px;
        grid-row: 1/-1;
        grid-column: -1/-2;
        align-self: center;
      }

      @media (min-width: 1280px) {
        width: 140px;
        height: 140px;
        // float: left;
        margin-left: -20px;
        margin-top: -50px;
        margin-right: 42px;
        grid-row: 1/2;
        grid-column: 1/2;
        align-self: initial;
      }
    }
  }

  &--icon_a::before {
    background-image: url("../../img/stack.svg#cat-slim");
  }

  &--icon_b::before {
    background-image: url("../../img/stack.svg#cat-pro");

    @media (min-width: 768px) {
      margin-left: 10px;
    }

    @media (min-width: 1280px) {
      margin-left: -20px;
    }
  }
}

.product-categories__title {
  font-family: var(--block-font-special);
  font-size: 24px;
  line-height: calc(37 / 24);
  font-weight: 400;
  color: var(--basic-black, #000000);
  text-transform: uppercase;
  margin-bottom: 30px;
  margin-top: 10px;

  @media (min-width: 768px) {
    font-size: 36px;
    line-height: 1;
    margin-bottom: 8px;
    margin-top: 10px;
  }

  @media (min-width: 1280px) {
    margin-bottom: 58px;
    margin-top: 0;
    grid-row: 1/2;
    grid-column: 2/3;
  }
}

.product-categories__link {
  display: inline-flex;
  align-items: center;
  font-family: var(--block-font-special);
  font-size: 16px;
  line-height: normal;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--basic-black, #000000);
  padding-top: 10px;

  @media (min-width: 768px) {
    font-size: 20px;
    margin-bottom: 18px;
  }

  @media (min-width: 1280px) {
    grid-row: -1/-2;
    grid-column: 1/-1;
  }

  &::after {
    content: "";
    display: inline-block;
    width: 33px;
    height: 33px;
    background-color: currentColor;
    mask: url("../../img/stack.svg#icon-arrow");
    mask-repeat: no-repeat;
    mask-position: -9px;
    margin-left: 15px;

    @media (min-width: 768px) {
      width: 46px;
      height: 30px;
      mask-position: -7px;
      margin-left: 25px;
    }

    @media (min-width: 1280px) {
      mask-position: -4px;
    }
  }

  &:active {
    opacity: 0.3;
  }

  &:hover::after {
    mask-position: 0;
    transition: mask-position 0.1s;
  }
}

.product-categories__text {
  padding-bottom: 8px;
  border-bottom: 1px solid var(--special-white, #d9d9d9);
  // margin-bottom: 11px;

  @media (min-width: 768px) {
    padding-bottom: 0;
    border: none;
  }

  @media (min-width: 1280px) {
    margin-bottom: 0;
    grid-column: 1/-1;
    grid-row: 2/3;
  }
}
